  transition: border .2s linear 0s, box-shadow .2s linear 0s;
  z-index: 1;
}

.ReactVirtualized__List:focus {
  border: 0;
  outline: none;
}

.ReactVirtualized__Grid__innerScrollContainer {
  padding-bottom: 10px;
}

.loading {
  background: rgba(255, 255, 255, 0.75);
  bottom: 0;
  display: block;
  left: 0;
  opacity: 0;
  position: absolute;
  right: 0;
  top: 0;
  transition: visibility 0s, opacity .5s linear;
  visibility: hidden;
}

.loading.show {
  opacity: 1;
  visibility: visible;
}

.loading .loading-content {
  padding-top: 30px;
  position: absolute;
  text-align: center;
  top: 20%;
  width: 100%;
}

.loading .loading-content .spinner {
  background: url("../images/ajax-loader.gif") no-repeat scroll center/25px 25px;
  height: 25px;
  margin: auto;
  padding: 5px;
  width: 25px;
}

.loading .loading-content span {
  font-size: large;
  font-weight: bold;
}




